@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700,800,900&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html, body, #root {
  height: 100vh;  
}

body {
  background: #fff
}

body, input, button {
  font-family: Poppins, sans-serif;
}
aside {
  width: 320px;
  background: #fff;
  box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0,02);
  border-radius: 3px;
  padding: 30px 20px
}

aside strong {
  font-size: 22px;
  text-align: center;
  display: block;
  color: #20103c;
  margin-bottom: 20px;
  font-weight: bold;
}
#main {
  max-width: 1700px;
  margin: 0 auto;
  padding: 30px 30px;
  display: flex;     
  justify-content: center;
}

aside {  
  padding-top: 40px;
  width: 350px;
  height: 490px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 8px 8px 5px rgba(85, 58, 162, 0.3);
  border: 1px solid rgba(85, 58, 162, 0.3);
  border-top: 2px solid rgba(85, 58, 162, 0.2);
  border-left: 2px solid rgba(85, 58, 162, 0.2);
  border-radius: 15px;
}


main {
  flex: 1 1;
  margin-left: 20px;
}

main ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  gap: 20px;
  list-style: none;

}




main {
  flex: 1 1;
  margin-left: 20px;
}

main ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  gap: 20px;
  list-style: none;

}

@media(max-width: 950px){ 

  aside {
    width: 100%;
  }
  main {
    width: 100%;
  }
  
  main ul {
    grid-template-columns: 1fr;
    ;
   }
 
}

@media(max-width: 1300px){
    #main {
      flex-direction: column;
    }

    #main main {
      margin-left: 0;
      margin-top: 30px;
    }

    aside {
      width: 100%;
    }
}


li.dev-item{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  grid-column-gap: 1px;
  -webkit-column-gap: 1px;
     -moz-column-gap: 1px;
          column-gap: 1px;
  background: #FFF;
  box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0,02);
  border-radius: 15px;
  width: 450px;
  padding: 20px;
  -webkit-transition: 0.1s;
  transition: 0.1s;
  box-shadow: 8px 8px 5px rgba(85, 58, 162, 0.3);
  border-top: 2px solid rgba(85, 58, 162, 0.2);
  border-left: 2px solid rgba(85, 58, 162, 0.2);
  border-bottom: 2px solid rgba(85, 58, 162, 0,8);
}

@media(max-width: 950px){   
li.dev-item{
  width: 100%;
}

  
}


li.dev-item header {
display: flex;
flex-direction: row;
align-items: center;

}

li.dev-item header img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  -webkit-transition: 0.3s;
  transition: 0.3s
}


li.dev-item img:hover  {
  -webkit-transform: scale(2.5);
          transform: scale(2.5);
  border-radius: 10%;
  margin-right: 50px;
 
}

li.dev-item header .user-info{
  margin-left: 10px;
  -webkit-transition: 0.1s;
  transition: 0.1s
}

.user-info strong {
  display: block;
  font-size: 19px;
  color: #333;
  }

  .user-info span {
    font-size: 14px;
    color: #999;
    margin-top: 2px;
  }

  li.dev-item p{  
    color: #666;
    font-size: 15px;
    line-height: 20px;
    margin: 10px 0
  }
 
  .dev-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    align-items: center;
    
  }
  
  .dev-buttons button {
    border: 0;
    background: #553AA2;
    padding: 5px;
    border-radius: 3px;
    font-size: 20px;
    color: #fff;
    cursor: pointer;    
    -webkit-transition: background 0.1s;    
    transition: background 0.1s;  
    display: flex;
  }

.dev-buttons .map-button {
    color: #eee;
    background-color: #777;
  }

  .dev-buttons .block-button {
    color: #eee;
    background-color: #777;
  }


  



